.section-about {
	padding: 144px 0 154px;

	html.no-webp & {
		background: url("../images/about-bg.jpg") top center no-repeat;
		background-size: cover;
	}

	html.webp & {
		background: url("../images/about-bg.webp") top center no-repeat;
		background-size: cover;
	}

	.title {
		margin-bottom: 68px;

		span {
			width: 170px;
		}
	}

	.advantages-item {
		&__icon {
			margin-bottom: 32px;
			width: 38px;
			height: 31px;
		}

		&__title {
			margin-bottom: 20px;
			font-size: 34px;
		}

		&__text {
			font-weight: 300;
			font-size: 16px;
			line-height: 24px;
		}
	}
}
