.modal {
	padding: 76px 69px 82px;
	width: 90%;
	max-width: 970px;
	background-color: #f9f6f1;

	.title {
		margin-bottom: 48px;

		span {
			width: 90px;
		}
	}

	ol {
		margin-bottom: 73px;
		margin-left: 20px;
		padding: 0;

		li {
			margin-bottom: 20px;
		}
	}

	.info-block {
		display: flex;

		&::before {
			content: "";
			display: inline-block;
			width: 62px;
			height: 34px;
			background-image: url("../images/info@2x.png");
			background-position-y: 2px;
			background-size: 32px 32px;
			background-repeat: no-repeat;
		}

		p {
			margin: 0;
			max-width: 555px;
		}
	}

	a.close-modal {
		right: 30px;
		top: 30px;
		background-image: url('data:image/png;base64,');
		background-size: 32px 32px;
	}
}
