.section-special-offers {
	padding: 106px 0;

	.title {
		margin-bottom: 55px;

		span {
			width: 100px;
		}
	}

	.offer-wrapper {
		padding-top: 46px;
		padding-bottom: 50px;
		padding-left: 170px;

		.offer-item {
			display: flex;
			align-items: center;
			border: 6px solid $color-red;
			padding-right: 90px;
			padding-left: 460px;
			min-height: 500px;
		}

		.offer__image {
			position: absolute;
			left: 0;
			top: 0;
		}

		.offer__content {
			.offer-title {
				margin-bottom: 18px;
				font-size: 34px;
			}

			.offer-text {
				margin-top: 0;
				margin-bottom: 26px;
				font-size: 17px;
				line-height: 24px;
			}

			.offer-condition {
				margin-top: 0;
				font-size: 14px;
			}

			.offer-coffee {
				position: absolute;
				right: -10px;
				bottom: -15px;
				width: 184px;
				height: 152px;
			}
		}
	}

	.owl-carousel-wrapper {
		.offer-nav {
			position: absolute;
			right: 15px;
			top: -10px;
			z-index: 100;
			display: flex;
			align-items: center;
			font-size: 24px;

			div,
			span {
				padding-bottom: 5px;
				color: $color-grey;
			}

			.current-offer {
				font-weight: 700;
				font-size: 36px;
				color: $color-text-dark;
			}

			.btn-next svg,
			.btn-prev svg {
				width: 32px;
				height: 13px;
				opacity: 0.4;

				&:hover {
					opacity: 1;
				}
			}

			.btn-prev {
				margin-right: 15px;
				transform: rotate(180deg);
			}

			.btn-next {
				margin-left: 15px;
			}
		}
	}
}
