.section-footer {
	padding: 45px 0 50px;
	background-color: $color-text-dark;

	.footer__info {
		padding-right: 88px;
	}

	.logo-wrapper {
		display: flex;
		align-items: center;
		margin-bottom: 49px;

		.logo {
			margin-right: 28px;
			width: 200px;
		}

		.copy {
			font-size: 16px;
			color: $color-grey;
		}
	}

	.link-wrap {
		margin-bottom: 19px;
	}

	a.link {
		color: $color-grey;
		opacity: 1;

		&:hover {
			color: $color-red;
			border-color: $color-red;;
		}
	}

	.link--pride {
		opacity: 0.4;

		span {
			color: $color-red;
		}

		&:hover {
			opacity: 1;
		}
	}

	.footer-title {
		margin-bottom: 30px;
		padding-top: 15px;
		font-weight: 500;
		font-size: 18px;
		color: $color-red;
	}

	.footer__contacts {
		color: $color-white;

		.phone {
			margin: 8px 0 0;
			color: $color-grey;
		}
	}

	.footer__nav {
		a {
			display: inline-block;
			margin-bottom: 17px;

			&:hover {
				color: $color-red;
			}
		}

		&-links {
			padding-right: 0;
		}
	}

	.footer-social {
		svg {
			margin-right: 19px;
			width: 18px;
			max-height: 19px;
			fill: $color-grey;

			&:hover {
				fill: $color-red;
			}
		}
	}
}
