.section-promo {
	padding: 17px 0 116px;

	html.no-webp & {
		background: url("../images/coffee-promo.png") no-repeat;
		background-position: -100px bottom;
		background-size: 307px 352px;
	}

	html.webp & {
		background: url("../images/coffee-promo.webp") no-repeat;
		background-position: -100px bottom;
		background-size: 307px 352px;
	}

	.promo {
		margin: 0 auto;
	}
}
