header.header {
	padding: 45px 0;
	min-height: 100vh;

	.container {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: calc(100% - 90px);
	}

	html.no-webp & {
		background: url("../images/header.jpg") top center no-repeat;
		background-size: cover;
	}

	html.webp & {
		background: url("../images/header.webp") top center no-repeat;
		background-size: cover;
	}


	.logo-wrapper {
		display: flex;
		align-items: center;

		.logo {
			width: 275px;
			max-width: 100%;
		}

		span {
			margin-left: 13px;
			width: 85px;
			height: 33px;
			font-weight: 500;
			font-size: 15px;
			line-height: 17px;
			color: $color-red;
		}
	}

	.nav {
		display: flex;
		justify-content: space-between;
		padding: 0 25px;

		a {
			letter-spacing: 0.4px;
			color: $nav-main-color;

			&:hover {
				color: $nav-hover-color;
			}
		}
	}

	.main-title {
		margin: 0;
		border-left: 6px solid #ef5646;
		padding-left: 45px;
		font-size: 54px;
		line-height: 72px;
		color: $color-white;
		text-transform: uppercase;
	}

	button.link-no-opacity::after {
		content: "+";
		position: absolute;
		right: -15px;
		top: 1px;
		font-weight: 600;
		color: #ef5646;
	}
}
